nav{
    height: 5rem;
    width: 100vw;
    background: var(--color-primary);
    display: grid;
    place-items: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
}
.nav__container{
    display: flex;
    height: 100%;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
/* only diplays on medium and small device */
.nav__toggle-btn{
    display: none;
}
.logo{
    width: 7rem;
    display: block;
}
.nav__links{
    display: flex;
    gap: 3.5rem;
    align-items: center;
}
.nav__links a{
    transition: var(--transition);
}
.nav__links a:hover{
    color: var(--color-secondary);
}
.active-nav{
    position: relative;
}
.active-nav::after{
    content:'';
    display: block;
    width: 1.2rem;
    height: 1.2rem;
    background: var(--color-primary);
    position: absolute;
    left: calc(50% - 0.6rem);
    transform: rotate(45deg);
    margin-top: 0.9rem;
}

/* MEDIA QUIERY */
@media (max-width: 1024px) {
    .nav__toggle-btn{
        display: inline-block;
        font-size: 1.8rem;
        background: transparent;
        cursor: pointer;
        color: var(--color-gray-100);
    }
    .active-nav::before, .active-nav::after{
        display: none;
    }
    .nav__links {
        position: absolute;
        flex-direction: column;
        top: 100%;
        right: 0;
        gap: 0;
    }
    .nav__links li{
        height: 4rem;
        width: 100%;
        box-shadow: -2rem 2rem 5rem rgba(0, 0, 0, 0.4);

    }
    .nav__links li a{
        background:  var(--color-primary);
        height: 100%;
        width: 100%;
        padding: 1rem 5rem 1rem 3rem;
        display: flex;
        align-items: center;
    }
    .show-nav{
        display: flex;
    }
    .hide-nav{
        display: none;
    }
}